<template>
  <div id="topbar-wrapper">
    <iconu icon-name="side" class="side-button" @click="handleChange"/>
    <route-title class="route-title" v-show="!flag"/>
  </div>
</template>
<script>
import routeTitle from './RouteTitle.vue'
export default {
  name: 'topBarOfMobile',
  components: {
    routeTitle
  },
  props: {
    flag: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleChange () {
      this.$emit('change', !this.flag)
    }
  }
}
</script>
<style scoped>
#topbar-wrapper {
  position: fixed;
  height: 3rem;
  right: 0;
  transition: top 0.2s ease-in-out;
  z-index: 50;
  border-bottom: 1px solid rgba(0,0,0,0.07);
  box-shadow: 0 3px 5px 0 rgb(0 0 0 / 5%);
  background-color: white;
}
.side-button {
  margin-left: .5rem;
  margin-top: .5rem;
  display: block;
  width: 1rem;
}
.route-title{
  text-align: center;
  margin-top: -1.5rem;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: keep-all;
  white-space: nowrap;
}
</style>



