<template>
  <el-container>
    <el-aside :style="sideStyle" v-show="flag">
      <side :style="sideStyle" @handleTitle="handleSideTitle"/>
    </el-aside>
    <el-container>
      <top-bar-mobile :style="topStyle" @change="changeSide" :flag="flag"/>
      <el-main style="padding-top: 2.2rem;">
        <el-container>
          <el-main>
            <router-view/>
          </el-main>
          <el-footer></el-footer>
        </el-container>
      </el-main>
      <el-footer>
        <foot/>
      </el-footer>
    </el-container>
  </el-container>
</template>
<script>
import Foot from './Foot.vue'
import TopBarMobile from './TopBarMobile.vue'
import Side from './Side.vue'
export default {
  name: 'basicMobile',
  components: {
    Foot,
    TopBarMobile,
    Side
  },
  data () {
    return {
      sideStyle: {},
      topStyle: {
        width: '1px'
      },
      flag: false
    }
  },
  mounted () {
    window.addEventListener('resize', this.sideResize, true)
    this.sideResize()
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.sideResize, true)
  },
  methods: {
    sideResize () {
      const width = Math.min(window.innerWidth * 0.73, 720)
      const height = window.innerHeight
      console.log(height)
      this.sideStyle = {
        width: width + 'px',
        height: height + 'px'
      }
      this.topStyle = {
        width: this.flag ? (window.innerWidth - width) + 'px' : window.innerWidth + 'px'
      }
    },
    changeSide (flag) {
      this.flag = flag
      const width = Math.min(window.innerWidth * 0.73, 720)
      this.topStyle.width = this.flag ? (window.innerWidth - width) + 'px' : window.innerWidth + 'px'
    },
    handleSideTitle (title) {
      this.flag = false
      this.changeSide()
    }
  }
}
</script>
<style scoped>
.change-context{
  font-size: .8rem;
  cursor: pointer;
  line-height: 1.2rem;
  width: 95%;
  max-width: 1045px;
  border-top: 1px solid #F3F3F3;
  margin-bottom: 1rem;
}
</style>



